
.about{
    background-color: #041F31;
    padding-left: 10rem;
    padding-right: 10rem;
}

.about-container1{
    display: flex;
    gap: 10px;
}


.about-p1{
    font-family: 'Montserrat';
font-size: 24px;
font-weight: 600;
line-height: 26px;
letter-spacing: 0.04em;
text-align: left;
color: #BDEBEA;
margin: 0;
padding-left: 15px;

}


.about-p2{
    font-family: 'Montserrat';
font-size: 18px;
font-weight: 400;
line-height: 26px;
letter-spacing: 0.04em;
text-align: left;
margin-bottom: 0px;
color: #BDEBEA;
}

.about-line{
    width: 6px;
height: 120px;

gap: 0px;
border-radius: 2px 0px 0px 0px;
opacity: 0px;

background: linear-gradient(90deg, #00F5A0 0%, #00D9F5 100%);

}

@media screen and (max-width: 900px){
    .about{
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 0;
    }

    .about-container1{
        width: 100%;
    }

    .about-line{
        width: 20px;
        height: 120px;
    }

    .about-p1{
        font-size: 24px;
        font-weight: 600;
        line-height: 26px;
        letter-spacing: 0.04em;
        text-align: left;
        margin: 20px;
        padding: 0;
    }

    .about-p2{
        font-size: 18px;
        font-weight: 400;
        line-height: 26px;
        letter-spacing: 0.04em;
        text-align: left;
        margin: 0px;
    }

    .about-section{
        padding: 20px;
    }
}